import React, { useState } from 'react';
import '../Where.css';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth<800;

const CutImage  = React.memo(
    (props)=>{
        let [selectSector, setSelectSector] = useState('');
        let {holderPart, event} = props
        return (
            <>
            {width?
                <svg className='svg1'  viewBox='0 0 600 300' preserveAspectRatio='xMinYMin meet' xmlns='http://www.w3.org/2000/svg'>
                    {/* Created with Method Draw - http://github.com/duopixel/Method-Draw/ */}
                    <g>
                        <rect x={-1} y={-1} width={532} height={358} id='canvas_background' fill='#f8f8f8' />
                        <g id='canvasGrid' display='none'>
                            <rect id='svg_1' width={610} height={504} x={1} y={1} strokeWidth={0} fill='url(#gridpattern)' />
                        </g>
                    </g>
                    <g>
                        <g>
                            <rect onClick={()=>{setSelectSector('A'); holderPart('A', event)}} className='pointer-text' stroke='#191e28' id='svg_2' height='202.999993' width='478.000015' y='55.4375' x='25.499986' strokeWidth={0} fill={selectSector==='A'?'red':'#191e28'} />
                        </g>
                        <text fill='#ffffff' stroke='#ffffff' strokeWidth={0} x='259.5' y='166.4375' id='svg_43' fontSize={17} fontFamily='Helvetica, Arial, sans-serif' textAnchor='start' xmlSpace='preserve'>A</text>
                    </g>
                </svg>
                :
                <svg width={530} height={356} xmlns='http://www.w3.org/2000/svg'>
                    {/* Created with Method Draw - http://github.com/duopixel/Method-Draw/ */}
                    <g>
                        <rect x={-1} y={-1} width={532} height={358} id='canvas_background' fill='#f8f8f8' />
                        <g id='canvasGrid' display='none'>
                            <rect id='svg_1' width={610} height={504} x={1} y={1} strokeWidth={0} fill='url(#gridpattern)' />
                        </g>
                    </g>
                    <g>
                        <g>
                            <rect onClick={()=>{setSelectSector('A'); holderPart('A', event)}} className='pointer-text' stroke='#191e28' id='svg_2' height='202.999993' width='478.000015' y='55.4375' x='25.499986' strokeWidth={0} fill={selectSector==='A'?'red':'#191e28'} />
                        </g>
                        <text fill='#ffffff' stroke='#ffffff' strokeWidth={0} x='259.5' y='166.4375' id='svg_43' fontSize={17} fontFamily='Helvetica, Arial, sans-serif' textAnchor='start' xmlSpace='preserve'>A</text>
                    </g>
                </svg>
            }
            </>
           )
    }
)

export default CutImage;
